import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';

import Google from 'assets/images/Icons/Google.png';
import Facebook from 'assets/images/Icons/Facebook.png';

import PiggySignUp from './PiggySignUp';

class PiggyFillForm extends Component {
  render() {
    const { onClick, category } = this.props;

    const mainText = category === 'life' || category === 'endowment' || category === 'retirement' ?
      'Earn extra $5 Credits instantly!' :
      'Sign in to get 5% Cashback on your purchase!';

    const descText = category === 'life' || category === 'endowment' || category === 'retirement' ?
      'Submit the form below as a PolicyPal user to earn $5 Credits in your account.' :
      'Purchase as a PolicyPal user to earn 5% Cashback in your account within 14 days.';

    return (
      <PiggySignUp
        header={
          <p className="main-text">{mainText}</p>
        }
        subHeader={
          <p className="desc-text">{descText}</p>
        }
        buttonInner={
          <Fragment>
            <span>Sign in to PolicyPal account</span>
            <img src={Facebook} alt="Facebook.png" />
            <img src={Google} alt="Google.png" />
          </Fragment>
        }
        onClick={onClick}
      />
    );
  }
}

PiggyFillForm.defaultProps = {
  onClick: () => {},
};

PiggyFillForm.propTypes = {
  onClick: PropTypes.func,
  category: PropTypes.string.isRequired,
};

export default PiggyFillForm;
