/**
 * DetailForm.js
 * Wrapper that contains the piggy login and the
 * right container to checkout or choose plan
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import $ from 'jquery';
import window from 'global/window';
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

// Import Insurer Logos
import PiggyFillForm from 'components/Templates/PiggySignUp/PiggyFillForm';

import Button from 'components/FormsV3/Button/Button';
import Selection from '../../Forms/Selection/Selection';
import Enquiry from '../../Templates/Enquiry/Enquiry';

import './DetailForm.scss';

class DetailForm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      rightStickyStyle: 'right-scroll',
    };

    this.handlePreSubmit = this.handlePreSubmit.bind(this);
    this.handleScroll = this.handleScroll.bind(this);
    this.renderSignUp = this.renderSignUp.bind(this);
    this.renderSignUpButton = this.renderSignUpButton.bind(this);
    this.renderUpperRightContainer = this.renderUpperRightContainer.bind(this);
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll);
    window.scrollTo(0, 0);
    this.handleScroll();

    if ($('#intercom-container')) {
      $('#intercom-container').hide();
    }
  }

  componentWillUnmount() {
    if ($('#intercom-container')) {
      $('#intercom-container').show();
    }

    window.removeEventListener('scroll', this.handleScroll);
  }

  handlePreSubmit(e) {
    const { onSubmit, login } = this.props;
    if (e.selection_checkbox && e.selection_checkbox.length === 1) {
      login.loginContainer.setSuccessCallback(() => onSubmit(e));
      login.loginContainer.setInitialIndex(2);
      login.popup.openPopup();
      return;
    }
    onSubmit(e);
  }

  handleScroll() {
    if (this.upperMarker) {
      const { rightStickyStyle } = this.state;
      const stickyStyle = this.upperMarker.getBoundingClientRect().y < 84 ? 'right-sticky' : 'right-scroll';

      if (rightStickyStyle !== stickyStyle) {
        this.setState({
          rightStickyStyle: stickyStyle,
        });
      }
    }
  }

  renderSignUp() {
    const {
      isLoggedIn,
      login,
      piggyCallback,
      category,
    } = this.props;

    if (!isLoggedIn) {
      return (
        <Row className="detail-upper-container" noGutters>
          <Col xs={12}>
            <PiggyFillForm
              category={category}
              onClick={() => {
                login.loginContainer.setSuccessCallback(piggyCallback);
                login.loginContainer.setInitialIndex(1);
                login.popup.openPopup();
              }}
            />
          </Col>
        </Row>
      );
    }

    return null;
  }

  renderSignUpButton(isPlanTotal) {
    const { isLoggedIn, form, change } = this.props;
    const signUpStyle = isPlanTotal ? '' : 'no-total';

    if (!isLoggedIn) {
      return (
        <div className={`sign-up ${signUpStyle}`}>
          <Selection
            form={form}
            change={change}
            name="selection_checkbox"
            type="checkbox"
            options={[
              { key: '1', description: 'Sign Up' },
            ]}
            sizeInSM={12}
          />
        </div>
      );
    }

    return null;
  }

  renderUpperRightContainer() {
    const { rightStickyStyle } = this.state;

    const {
      changePlan,
      planDetails,
      handleSubmit,
      checkoutButtonDesc,
    } = this.props;

    return (
      <Col
        className={`upper-right-container ${rightStickyStyle}`}
      >
        <div ref={node => this.upperRightContainer = node} />
        <div className="logo-container">
          <div className="logo-inner-container">
            <img className="company-logo" src={planDetails.logo} alt={`${planDetails.logoDesc}.png`} />
          </div>
          <p>{planDetails.planName}</p>
        </div>
        <div className="submit-container">
          <div className="sign-up-container">
            {
              planDetails.planTotal ?
                <div className="cost-container">
                  <p className="total-text">Total</p>
                  <p className="cost-text">{planDetails.planTotal}</p>
                </div>
              :
                null
            }
            {this.renderSignUpButton(planDetails.planTotal)}
            <Button
              variant="medium-flat-green"
              width={130}
              height={48}
              onClick={handleSubmit(this.handlePreSubmit)}
            >
              <p>{checkoutButtonDesc}</p>
            </Button>
            {
              changePlan ?
                <Button
                  variant="medium-grey"
                  width={130}
                  height={48}
                  onClick={() => changePlan()}
                >
                  <p>Change Plan</p>
                </Button>
              :
                null
            }
          </div>
        </div>
      </Col>
    );
  }

  render() {
    const { errorArray } = this.props;
    return (
      <Fragment>
        <Row className="detail-main-container" noGutters>
          <div ref={node => this.upperMarker = node} />
          <Col xs={12} className="detail-left-container">
            {this.renderSignUp()}
            {this.props.children}
            {errorArray}
          </Col>
          {this.renderUpperRightContainer()}
          <Enquiry marginTop={30} />
        </Row>
      </Fragment>
    );
  }
}

DetailForm.defaultProps = {
  errorArray: null,
  checkoutButtonDesc: 'Checkout',
};

DetailForm.propTypes = {
  handleSubmit: PropTypes.func.isRequired,
  onSubmit: PropTypes.func.isRequired,
  change: PropTypes.func.isRequired,
  form: PropTypes.string.isRequired,
  piggyCallback: PropTypes.func.isRequired,
  planDetails: PropTypes.object.isRequired,
  changePlan: PropTypes.func.isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
  errorArray: PropTypes.array,
  login: PropTypes.object.isRequired,
  checkoutButtonDesc: PropTypes.string,
  category: PropTypes.string.isRequired,
};

const mapStateToProps = state => ({
  login: state.login,
});

export default connect(mapStateToProps)(DetailForm);
