/**
 * PADetail.js
 * Detail View for PA
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import document from 'global/document';
import window from 'global/window';
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { reduxForm, getFormValues } from 'redux-form';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

// Import Insurer Logos
import DetailForm from '../Templates/DetailForm/DetailForm';
import TabView from '../Templates/TabView/TabView';

import TextInput from '../Forms/TextInput/TextInput';
import DatePickerV2 from '../Forms/DatePicker/DatePickerV2';
import DropDownV2 from '../Forms/DropDown/DropDownV2';
import Selection from '../Forms/Selection/Selection';

import { validateForm, dateReValidate } from '../../lib/validators';
import { fbTag, gaEvent, ampTag } from '../../lib/tagFunctions';
// import { getPostal } from '../../actions/insuranceActions';

const commonFields = [
  'name',
  'nationality',
  'id_type',
  'nric',
  'birth_date',
  'gender',
];

const myselfFields = [
  'email',
  'postal',
  'mobile_number',
  'race',
  'marital_status',
];

const myselfSpouseFields = [
  'occupation',
  'industry',
];

const childFields = [
  'education',
];

const formName = 'pa-detail';

class PADetail extends Component {
  constructor(props) {
    super(props);

    const tabsNames = this.generateTabsNames();

    this.state = {
      errorStringData: {},
      errorString: null,
      tabs: tabsNames.tabs,
      formNames: tabsNames.formNames,
      prefix: '',
    };

    this.handleSubmit = this.handleSubmit.bind(this);
    this.validateForm = this.validateForm.bind(this);
  }

  getFormFields(prefix, fields) {
    return _.map(fields, field => (`${prefix}${field}`));
  }

  async validateForm(e/* , isSubmit = false */) {
    // console.log(e);
    const { formNames } = this.state;

    const errorPersons = [];
    let errorString = null;
    let errorTabIndex = -1;

    const errorResult = _.reduce(formNames, (result, name, index) => {
      let newResult = {};
      let displayName = '';
      if (name === '') {
        const myself = [
          ...this.getFormFields(name, commonFields),
          ...this.getFormFields(name, myselfFields),
          ...this.getFormFields(name, myselfSpouseFields),
        ];

        newResult = validateForm(e, myself);
        displayName = 'Myself';
      } else if (name === 'spouse-') {
        const spouse = [
          ...this.getFormFields(name, commonFields),
          ...this.getFormFields(name, myselfSpouseFields),
        ];

        newResult = validateForm(e, spouse);
        displayName = 'Spouse';
      } else {
        const child = [
          ...this.getFormFields(name, commonFields),
          ...this.getFormFields(name, childFields),
        ];

        newResult = validateForm(e, child);
        displayName = `Child ${name.substring(_.findIndex(name, char => char === '_') + 1, name.length - 1)}`;
      }

      if (!_.isEmpty(newResult)) {
        errorPersons.push(displayName);
        if (errorTabIndex === -1) {
          errorTabIndex = index;
        }
      }

      return { ...result, ...newResult };
    }, {});

    /* if (isSubmit) {
      const postalResult = await getPostal(e.postal);
      if (!postalResult) {
        errorResult.postal = 'Invalid postal code';

        if (errorTabIndex === -1) {
          errorTabIndex = 0;
        }

        if (!_.find(errorPersons, person => person === 'Myself')) {
          errorPersons.push('Myself');
        }
      }
    } */

    this.setState({
      errorStringData: errorResult,
    });

    if (errorPersons.length > 0) {
      errorString = 'Errors found in form(s): ';
      for (let i = 0; i < errorPersons.length; ++i) {
        const comma = i !== errorPersons.length - 1 ? ', ' : '';
        errorString = `${errorString}${errorPersons[i]}${comma}`;
      }

      this.setState({
        errorString: [<p className="error-string" key="error-1">{errorString}</p>],
      });
    } else {
      this.setState({
        errorString: null,
      });
    }

    return { errorResult, errorTabIndex };
  }

  async handleSubmit(e, isFirstValidate = true) {
    // console.log(e);
    const { submitCallback, storeForm } = this.props;

    const validateResults = await this.validateForm(isFirstValidate ? e : storeForm, true);
    const isEmptyResult = _.isEmpty(validateResults.errorResult);

    if (dateReValidate(
      isEmptyResult,
      isFirstValidate,
      validateResults.errorResult,
      error => this.setState({ errorStringData: error, errorString: null }),
      () => this.handleSubmit(storeForm, false),
    )) {
      return;
    }

    if (isEmptyResult) {
      fbTag('PPS2 - ButtonClick - PersonalAccident - DetailForm_Checkout', { Product: 'PersonalAccident', Click_Text: 'Checkout' });
      gaEvent('PersonalAccident - DetailForm - Checkout', {
        event_category: 'PersonalAccident',
        event_label: 'PersonalAccident - DetailForm - Checkout',
      });
      ampTag('PACheckoutA', {
        idtype: e.id_type,
        nationality: e.nationality,
        gender: e.gender,
        marital: e.marital_status,
        race: e.race,
        occupation: e.occupation,
        industry: e.industry,
      });

      submitCallback();
    } else if (this.tabView && validateResults.errorTabIndex !== -1) {
      this.tabView.wrappedInstance.goToIndex(validateResults.errorTabIndex);
    }

    window.scrollTo(0, document.body.scrollHeight);
  }

  updatePrefix(name) {
    this.setState({
      prefix: name,
    });
  }

  generateTabsNames() {
    const { paForm } = this.props;
    const tabs = [];
    const formNames = [''];

    const isSpouse = paForm.plan_type === 'Me & My Spouse';
    const isFamily = paForm.plan_type === 'Me & My Family';

    tabs.push({
      desc: 'Me',
      onClick: () => { this.updatePrefix(''); },
    });

    if (isSpouse || (isFamily && paForm.family_members === '2 Adults')) {
      const name = 'spouse-';
      tabs.push({
        desc: 'Spouse',
        onClick: () => { this.updatePrefix(name); },
      });
      formNames.push(name);
    }

    if (isFamily) {
      for (let i = 0; i < paForm.num_children; ++i) {
        const name = `fam-child_${i + 1}-`;
        tabs.push({
          desc: `Child ${i + 1}`,
          onClick: () => { this.updatePrefix(name); },
        });
        formNames.push(name);
      }
    }

    return {
      tabs: tabs.length > 1 ? tabs : null,
      formNames,
    };
  }

  renderEmail() {
    const { errorStringData, prefix } = this.state;
    const {
      form,
      change,
      storeForm,
    } = this.props;

    if (prefix === '') {
      return (
        <Col xs={12}>
          <TextInput
            form={form}
            change={change}
            name={`${prefix}email`}
            placeholderString=""
            size="medium"
            zIndex={99}
            desc="Email Address"
            errorString={errorStringData[`${prefix}email`]}
            validateForm={() => this.validateForm(storeForm)}
          />
        </Col>
      );
    }

    return null;
  }

  renderOccupationIndustry() {
    const { errorStringData, prefix } = this.state;
    const {
      form,
      change,
      constants,
      storeForm,
    } = this.props;

    if (prefix === '' || prefix === 'spouse-') {
      return (
        <Fragment>
          <Col className="right-padding" xs={12} sm={6}>
            <DropDownV2
              form={form}
              change={change}
              name={`${prefix}occupation`}
              placeholderString="Select occupation"
              dropList={constants.occupations}
              size="medium"
              zIndex={92}
              desc="Occupation"
              errorString={errorStringData[`${prefix}occupation`]}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
          <Col className="left-padding" xs={12} sm={6}>
            <DropDownV2
              form={form}
              change={change}
              name={`${prefix}industry`}
              placeholderString="Select industry"
              dropList={constants.industries}
              size="medium"
              zIndex={91}
              desc="Industry"
              errorString={errorStringData[`${prefix}industry`]}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
        </Fragment>
      );
    }

    return null;
  }

  renderPostalMobile() {
    const { errorStringData, prefix } = this.state;
    const {
      form,
      change,
      storeForm,
    } = this.props;

    if (prefix === '') {
      return (
        <Fragment>
          <Col className="right-padding" xs={12} sm={6}>
            <TextInput
              form={form}
              change={change}
              name={`${prefix}postal`}
              placeholderString=""
              size="medium"
              zIndex={90}
              desc="Postal Code"
              errorString={errorStringData[`${prefix}postal`]}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
          <Col className="left-padding" xs={12} sm={6}>
            <TextInput
              form={form}
              change={change}
              name={`${prefix}mobile_number`}
              placeholderString=""
              size="medium"
              zIndex={89}
              desc="Mobile Number"
              errorString={errorStringData[`${prefix}mobile_number`]}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
        </Fragment>
      );
    }

    return null;
  }

  renderEducation() {
    const { errorStringData, prefix } = this.state;
    const {
      form,
      change,
      storeForm,
    } = this.props;

    if (prefix !== '' && prefix !== 'spouse-') {
      return (
        <Col className="right-padding" xs={12} sm={6}>
          <Selection
            form={form}
            change={change}
            name={`${prefix}education`}
            type="text-selection"
            noEmptyTextSelection
            options={[
              { key: 'Yes', description: 'Yes' },
              { key: 'No', description: 'No' },
            ]}
            sizeInSM={6}
            desc="Full school education"
            errorString={errorStringData[`${prefix}education`]}
            validateForm={() => this.validateForm(storeForm)}
          />
        </Col>
      );
    }

    return null;
  }

  renderCenterPortion() {
    const { errorStringData, prefix } = this.state;
    const {
      form,
      change,
      constants,
      storeForm,
    } = this.props;

    if (prefix === '') {
      return (
        <Fragment>
          <Col className="right-padding" xs={12} sm={6}>
            <DropDownV2
              form={form}
              change={change}
              name={`${prefix}race`}
              placeholderString="Select race"
              dropList={constants.race}
              size="medium"
              zIndex={94}
              desc="Race"
              errorString={errorStringData[`${prefix}race`]}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
          <Col className="left-padding" xs={12} sm={6}>
            <Selection
              form={form}
              change={change}
              name={`${prefix}gender`}
              type="text-selection"
              noEmptyTextSelection
              options={[
                { key: 'Male', description: 'Male' },
                { key: 'Female', description: 'Female' },
              ]}
              sizeInSM={6}
              desc="Gender"
              errorString={errorStringData[`${prefix}gender`]}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
          <Col className="right-padding" xs={12} sm={6}>
            <DatePickerV2
              form={form}
              change={change}
              name={`${prefix}birth_date`}
              size="medium"
              zIndex={93}
              desc="Date of birth"
              errorString={errorStringData[`${prefix}birth_date`]}
              validateForm={() => this.validateForm(storeForm)}
              endDate={new Date()}
            />
          </Col>
          <Col className="left-padding" xs={12} sm={6}>
            <Selection
              form={form}
              change={change}
              name={`${prefix}marital_status`}
              type="text-selection"
              noEmptyTextSelection
              options={[
                { key: 'Single', description: 'Single' },
                { key: 'Married', description: 'Married' },
              ]}
              sizeInSM={6}
              desc="Marital Status"
              errorString={errorStringData[`${prefix}marital_status`]}
              validateForm={() => this.validateForm(storeForm)}
            />
          </Col>
        </Fragment>
      );
    }

    return (
      <Fragment>
        <Col className="right-padding" xs={12} sm={6}>
          <DatePickerV2
            form={form}
            change={change}
            name={`${prefix}birth_date`}
            size="medium"
            zIndex={93}
            desc="Date of birth"
            errorString={errorStringData[`${prefix}birth_date`]}
            validateForm={() => this.validateForm(storeForm)}
            endDate={new Date()}
          />
        </Col>
        <Col className="left-padding" xs={12} sm={6}>
          <Selection
            form={form}
            change={change}
            name={`${prefix}gender`}
            type="text-selection"
            noEmptyTextSelection
            options={[
              { key: 'Male', description: 'Male' },
              { key: 'Female', description: 'Female' },
            ]}
            sizeInSM={6}
            desc="Gender"
            errorString={errorStringData[`${prefix}gender`]}
            validateForm={() => this.validateForm(storeForm)}
          />
        </Col>
      </Fragment>
    );
  }

  renderForm() {
    const { errorStringData, prefix } = this.state;
    const {
      form,
      change,
      constants,
      storeForm,
    } = this.props;

    return (
      <Row className="main-form" noGutters>
        {this.renderEmail()}
        <Col className="right-padding" xs={12} sm={6}>
          <TextInput
            form={form}
            change={change}
            name={`${prefix}name`}
            placeholderString=""
            size="medium"
            zIndex={98}
            desc="Full Name"
            errorString={errorStringData[`${prefix}name`]}
            validateForm={() => this.validateForm(storeForm)}
          />
        </Col>
        <Col className="left-padding" xs={12} sm={6}>
          <DropDownV2
            form={form}
            change={change}
            name={`${prefix}nationality`}
            placeholderString="Select nationality"
            dropList={constants.nationalities}
            size="medium"
            zIndex={97}
            desc="Nationality"
            errorString={errorStringData[`${prefix}nationality`]}
            validateForm={() => this.validateForm(storeForm)}
          />
        </Col>
        <Col className="right-padding" xs={12} sm={6}>
          <DropDownV2
            form={form}
            change={change}
            name={`${prefix}id_type`}
            placeholderString="Select ID Type"
            dropList={constants.idTypes}
            size="medium"
            zIndex={96}
            desc="Type of ID"
            errorString={errorStringData[`${prefix}id_type`]}
            validateForm={() => this.validateForm(storeForm)}
          />
        </Col>
        <Col className="left-padding" xs={12} sm={6}>
          <TextInput
            form={form}
            change={change}
            name={`${prefix}nric`}
            placeholderString=""
            size="medium"
            zIndex={95}
            desc="ID Number"
            errorString={errorStringData[`${prefix}nric`]}
            validateForm={() => this.validateForm(storeForm)}
            toUpper
          />
        </Col>
        {this.renderCenterPortion()}
        {this.renderOccupationIndustry()}
        {this.renderPostalMobile()}
        {this.renderEducation()}
      </Row>
    );
  }

  renderInner() {
    const { tabs } = this.state;

    if (!tabs) {
      return this.renderForm();
    }

    return (
      <TabView
        maxTabWidthPx={128}
        ref={node => this.tabView = node}
        className="tabview-margin-top"
        tabs={tabs}
        isDataFadeOnly
        name="pa-detail-tab"
      >
        {this.renderForm()}
      </TabView>
    );
  }

  render() {
    const { errorString } = this.state;
    const {
      form,
      change,
      planDetails,
      isLoggedIn,
      piggyCallback,
      changePlan,
    } = this.props;

    return (
      <DetailForm
        category="pa"
        changePlan={changePlan}
        handleSubmit={this.props.handleSubmit}
        onSubmit={this.handleSubmit}
        change={change}
        form={form}
        planDetails={planDetails}
        isLoggedIn={isLoggedIn}
        piggyCallback={piggyCallback}
        errorArray={errorString}
      >
        {this.renderInner()}
      </DetailForm>
    );
  }
}

PADetail.defaultProps = {
  storeForm: null,
};

PADetail.propTypes = {
  paForm: PropTypes.object.isRequired,
  handleSubmit: PropTypes.func.isRequired,
  submitCallback: PropTypes.func.isRequired,
  form: PropTypes.string.isRequired,
  change: PropTypes.func.isRequired,
  planDetails: PropTypes.object.isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
  piggyCallback: PropTypes.func.isRequired,
  changePlan: PropTypes.func.isRequired,
  constants: PropTypes.object.isRequired,
  storeForm: PropTypes.object,
};

const mapStateToProps = state => ({
  storeForm: getFormValues(formName)(state),
  constants: state.insurance.constants,
});

export default reduxForm({
  form: formName,
  destroyOnUnmount: false,
})(connect(mapStateToProps)(PADetail));
