/**
 * PADetailContainer.js
 * The Detail container for PA
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import { getFormValues } from 'redux-form';
import PropTypes from 'prop-types';

// Import Insurer Logos
import Logos from 'assets/images/InsurerLogos';

import PADetail from '../../components/PA/PADetail';
import { setInsuranceFormState, FORMSTATES } from '../../actions/insuranceActions';
import { fbTag, gaTag, gaEvent, ampTag } from '../../lib/tagFunctions';

class PADetailContainer extends Component {
  componentDidMount() {
    const { setFormState } = this.props;
    setFormState(FORMSTATES.DETAILFORM.index);
    fbTag('PPS2 - Pageview - PersonalAccident - DetailForm', { Product: 'PersonalAccident' });
    gaTag({
      page_title: 'PersonalAccident - Detail Form',
      page_path: '/personal-accident/detailform',
    });
    ampTag('PADetailsS');
  }

  render() {
    const {
      plansData,
      isLoggedIn,
      setFormState,
      profileValues,
      piggyCallback,
      paForm,
      history,
    } = this.props;

    const selectedData = plansData;

    let planDetails = {
      logo: Logos.others,
      logoDesc: '',
      planName: '',
      planTotal: null,
    };

    try {
      planDetails = {
        logo: Logos[selectedData.info.insurer_slug],
        logoDesc: `${selectedData.info.insurer_name}.svg`,
        planName: selectedData.info.product_name,
        planTotal: `$${selectedData.policyInfo[selectedData.planType].net_price.toFixed(2)}`,
      };
    } catch (error) {
      console.log('Get plan details failed');
    }

    return (
      <PADetail
        paForm={paForm}
        selectedData={selectedData}
        planDetails={planDetails}
        isLoggedIn={isLoggedIn}
        piggyCallback={piggyCallback}
        changePlan={() => {
          history.goBack();
          fbTag('PPS2 - ButtonClick - PersonalAccident - DetailForm_ChangePlan', { Product: 'PersonalAccident', Click_Text: 'ChangePlan' });
          gaEvent('PersonalAccident - DetailForm - ChangePlan', {
            event_category: 'PersonalAccident',
            event_label: 'PersonalAccident - DetailForm - ChangePlan',
           });
        }}
        profileValues={profileValues}
        submitCallback={() => {
          setFormState(FORMSTATES.CHECKOUT.index);
          history.push(`/personal-accident${FORMSTATES.CHECKOUT.path}`);
        }}
      />
    );
  }
}

PADetailContainer.defaultProps = {
};

PADetailContainer.propTypes = {
  plansData: PropTypes.object.isRequired,
  piggyCallback: PropTypes.func.isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
  setFormState: PropTypes.func.isRequired,
  profileValues: PropTypes.object.isRequired,
  paForm: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
};


const mapStateToProps = state => ({
  paForm: getFormValues('pa')(state),
  profileValues: state.userDetails,
  plansData: state.pa.selectedPlanData,
});

const mapDispatchToProps = dispatch => ({
  setFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
});

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(PADetailContainer));
